html, body, #app {
  width: 100%;
  height: 100%;
  background: #f0f8ff;
  margin: 0;
  padding: 0;
}

body, button, input {
  font-family: Monaco, Courier New, Courier, monospace;
  font-size: 12px;
}

a, a:visited, a:link {
  color: #00f;
}

a:hover {
  color: #6495ed;
}

table#conditions-table {
  border-collapse: collapse;
}

table#conditions-table thead {
  color: #fff;
  background: #6495ed;
}

table#conditions-table th {
  vertical-align: bottom;
  padding: .2em;
}

table#conditions-table tr:nth-child(2n) {
  background: #f0f8ff;
}

table#conditions-table td {
  vertical-align: top;
  padding: .2em;
}

table#conditions-table td.modifier {
  vertical-align: middle;
  text-align: center;
}

